import React from "react";
import Video from "./startbootstrap-coming-soon-gh-pages/assets/mp4/bg.mp4";

const adminEmail = "mailto:nathan.snow@cqumail.com";
const heading = "Portfolio coming to this space soon";
const message =
  `I'm working hard to finish the development of my portfolio, but if you would like to 
   get in touch with me, please get in touch via social media or`;
const socialLinks = [
  { title: "GitGub", icon: "fab fa-github", href: "https://github.com/nathansnow1981" },
  { title: "Facebook", icon: "fab fa-facebook-f", href: "https://www.facebook.com/nathan.snow.399/" },
  { title: "Discord", icon: "fab fa-discord", href: "https://discordapp.com/users/821987974690373662" }
];

const ComingSoon = () => {
  return (
    <div>
      {/* Background Video */}
      <video className="bg-video" playsInline="playsinline" autoPlay="autoplay" muted="muted" loop="loop">
        <source src={Video} type="video/mp4" />
      </video>
      {/* Masthead */}
      <div className="masthead">
        <div className="masthead-content text-white">
          <div className="container-fluid px-4 px-lg-0">
            <h1 className="fst-italic lh-1 mb-4">{heading}</h1>
            <p className="mb-5">{message} <a className="email-link" href={adminEmail}> send me an email</a></p>
          </div>
              Thanks for your patience,<br/><br/>Nathan
        </div>
      </div>
      {/* Social Icons */}
      <div className="social-icons">
        <div className="d-flex flex-row flex-lg-column justify-content-center align-items-center h-100 mt-3 mt-lg-0">
            {socialLinks.map(link => 
                <a className="btn btn-dark m-3" href={link.href} title={link.title}><i className={link.icon}></i></a>
            )}
        </div>
      </div>
      {/* Scripts */}
      <script src="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/js/bootstrap.bundle.min.js"></script>
      <script src="https://cdn.startbootstrap.com/sb-forms-latest.js"></script>
    </div>
  );
};

export default ComingSoon;
